.s-product-docs {
    position: relative;
    display: flex;
    width: 100%;

    &__wrapper {
        width: calc(100% - (238px * 2));
        min-height: 810px;
        background: linear-gradient(
            357.79deg,
            #202020 1.85%,
            rgba(40, 46, 66, 0) 69.85%
        );
        padding: 0 50px;
        padding-top: 130px;
        margin: 0 auto;

        @media (max-width: 1199px) {
            width: calc(100% - (116px * 2));
            padding-top: 105px;
        }

        @media (max-width: 991px) {
            min-height: 579px;
            padding: 0 30px;
            padding-top: 70px;
        }

        @media (max-width: 767px) {
            width: 100%;
            min-height: 421px;
            padding: 0 15px;
            padding-top: 45px;
        }
    }

    &__info {
        position: relative;
        margin: 0 auto;
    }

    &__back {
        position: absolute;
        width: 238px;
        height: 100%;

        @media (max-width: 1199px) {
            width: 116px;
        }

        @media (max-width: 767px) {
            display: none;
        }

        &-right {
            right: 0;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__tabs {
        display: flex;
        align-items: center;
        margin-bottom: rem(58);
        flex-wrap: wrap;
        gap: rem(10) rem(25);

        @media (min-width: 768px) {
            justify-content: space-between;
            flex-wrap: nowrap;
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(113);
        }

        @media (max-width: 767px) {
            padding: 0 15px;
        }
    }

    &__tab {
        cursor: pointer;
        position: relative;
        padding: rem(8) 0;
        font-size: rem(17);
        line-height: rem(20);
        color: #4e5056;
        border: rem(2) solid transparent;
        transition: 0.4s;

        @media (min-width: 768px) {
            padding: rem(15) 0;
            font-size: rem(19);
            line-height: rem(22);
        }

        @media (min-width: 1024px) {
            padding: rem(18) 0;
            font-size: rem(25);
            line-height: rem(29);
        }

        @media (min-width: 1600px) {
            font-size: rem(38);
            line-height: rem(45);
        }

        @media (min-width: 1920px) {
            font-size: rem(40);
            line-height: rem(47);
        }

        @media (max-width: 767px) {
            display: flex;
            align-items: center;
        }

        &::before {
            @media (max-width: 767px) {
                position: static;
                left: 0;
                transform: translateY(0);
                display: block;
                opacity: 0;
                margin-right: 15px;
            }
        }

        &.is-active {
            color: #ceced0;
            border-bottom: rem(2) solid $red;

            &::before {
                position: absolute;
                top: 50%;
                left: -15px;
                width: rem(3);
                height: rem(16);
                transform: translateY(-50%);
                background-color: $red;
                content: "";

                @media (min-width: 768px) {
                    top: rem(75);
                    left: 0;
                    transform: none;
                }

                @media (min-width: 1024px) {
                    top: rem(95);
                }

                @media (min-width: 1600px) {
                    top: rem(120);
                }

                @media (max-width: 767px) {
                    opacity: 1;
                }
            }
        }

        &:hover {
            color: #ceced0;
            border-bottom: rem(2) solid $red;
        }
    }

    &__content {
        display: none;
        animation: show 0.4s ease-in-out;

        &.is-active {
            display: block;
        }

        &-wrapper {
            position: relative;
            display: flex;
            height: rem(175);
            margin: rem(40) 0;
            flex-direction: column;
            gap: rem(15);
            overflow-x: hidden;
            overflow-y: auto;

            @media (min-width: 1024px) {
                height: rem(400);
                gap: rem(50);
            }

            @media (min-width: 1600px) {
                height: rem(600);
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: rem(12);
        line-height: rem(14);
        color: #9a9ea9;
        gap: rem(15);

        @media (min-width: 1024px) {
            font-size: rem(17);
            line-height: rem(20);
        }

        @media (min-width: 1600px) {
            font-size: rem(21);
            line-height: rem(25);
        }

        &-description {
            font-size: rem(10);
            line-height: rem(14);
        }

        &-info {
            display: flex;
            align-items: center;
            padding-right: rem(20);
            gap: rem(10);

            @media (min-width: 1024px) {
                padding-right: rem(35);
                gap: rem(25);
            }
        }

        &-download {
            cursor: pointer;
            flex: none;

            svg {
                width: rem(20);
                height: rem(20);
                transition: 0.5s;
                fill: $white;

                @media (min-width: 1024px) {
                    width: rem(32);
                    height: rem(32);
                }

                @media (min-width: 1600px) {
                    width: rem(40);
                    height: rem(40);
                }
            }

            &:hover {
                svg {
                    fill: $red;
                }
            }
        }
    }
}
