.s-product-content {
    position: relative;
    overflow-x: visible;

    &::-webkit-scrollbar {
        display: none;
    }

    &__text {
        max-width: rem(540);
        margin: 0 auto;
        padding: rem(600) 0 0;
        font-size: rem(25);
        line-height: 1.3;
        color: #424346;

        @media (min-width: 768px) {
            font-size: rem(35);
        }

        @media (min-width: 1024px) {
            max-width: rem(610);
            font-size: rem(45);
        }

        @media (min-width: 1440px) {
            max-width: rem(964);
            font-size: rem(70);
        }

        @media (max-width: 1024px) {
            max-width: rem(1150);
            padding-top: 100px;
        }

        @media (max-width: 767px) {
            padding-top: 50px;
        }
    }

    &__image {
        position: -webkit-sticky;
        position: sticky;
        width: 100%;
        z-index: 1;
        bottom: -900px;

        @media (max-width: 1440px) {
            bottom: -600px;
        }

        @media (max-width: 1024px) {
            bottom: -400px;
        }

        @media (max-width: 767px) {
            bottom: 0;
            min-width: 400px;
        }

        img {
            width: 100%;
            height: auto;
        }
    }

    .l-default {
        position: relative;
        margin-top: 0;
        padding-top: 125px;
        padding-bottom: 138px;
        z-index: 2;
        display: block;

        @media (max-width: 1024px) {
            padding-top: 50px;
            padding-bottom: 50px;
        }
    }
}
