.s-product-video {
    position: relative;
    padding-top: rem(200);
    padding-bottom: rem(81);

    @media (max-width: 991px) {
        padding-bottom: rem(30);
    }

    &__wrapper {
        display: flex;
        align-items: flex-end;
    }

    &__left {
        margin-right: rem(127);
        padding-bottom: rem(40);

        @media (max-width: 1599px) {
            position: absolute;
            bottom: rem(90);
            left: rem(70);
            padding-bottom: 0;
        }

        @media (max-width: 1439px) {
            top: rem(180);
            bottom: initial;
        }

        @media (max-width: 991px) {
            left: rem(15);
        }

        @media (max-width: 710px) {
            top: rem(120);
        }

        @media (max-width: 710px) {
            top: rem(90);
            left: 0;
        }

        @media (max-width: 390px) {
            top: rem(60);
        }
    }

    &__right {
        margin-left: auto;
    }

    &__notebook {
        cursor: default;
        position: relative;
        margin-right: rem(-70);

        @media (max-width: 1439px) {
            margin-right: rem(-120);
        }

        @media (max-width: 991px) {
            margin-right: rem(-60);
            margin-bottom: rem(-30);
        }

        @media (max-width: 575px) {
            margin-right: rem(-40);
        }

        &-img {
            @media (max-width: 1599px) {
                max-width: rem(790);
            }

            @media (max-width: 1439px) {
                max-width: rem(1000);
            }

            @media (max-width: 1200px) {
                max-width: rem(900);
            }

            @media (max-width: 991px) {
                max-width: rem(820);
            }

            @media (max-width: 839px) {
                max-width: rem(750);
            }

            @media (max-width: 767px) {
                max-width: rem(700);
            }

            @media (max-width: 575px) {
                max-width: rem(480);
            }

            @media (max-width: 475px) {
                max-width: rem(370);
            }

            @media (max-width: 375px) {
                max-width: rem(320);
            }
        }

        &-monitor {
            z-index: 1;
            cursor: pointer;
            position: absolute;
            top: 5%;
            left: 46%;
            width: 60%;
            height: 65%;
            background: #000;
            opacity: 0;
            transform: translateX(-46%);
            content: "";
        }
    }

    &__link {
        display: flex;
        align-items: center;

        @media (max-width: 710px) {
            justify-content: center;
            flex-direction: column-reverse;
            gap: rem(10);
        }

        &-text {
            margin-right: rem(40);
            font-size: rem(40);
            line-height: 1;
            white-space: nowrap;

            @media (max-width: 1599px) {
                font-size: rem(38);
                line-height: rem(45);
            }

            @media (max-width: 1439px) {
                max-width: rem(115);
                margin-right: rem(25);
                font-size: rem(19);
                line-height: rem(22);
                white-space: pre-line;
            }

            @media (max-width: 768px) {
                max-width: 100%;
                margin-right: rem(15);
                font-size: rem(12);
                line-height: rem(14);
            }

            @media (max-width: 710px) {
                max-width: rem(90);
                margin-right: 0;
                text-align: center;
            }
        }

        &-icon {
            width: rem(56);

            @media (max-width: 1439px) {
                width: rem(48);
            }

            @media (max-width: 991px) {
                width: rem(24);
            }
        }
    }

    &__text {
        display: flex;
        align-items: flex-start;
        width: 50%;
        margin-top: rem(31);
        margin-right: rem(100);
        margin-left: auto;
        font-weight: 300;

        @media (max-width: 1599px) {
            margin-right: 0;
        }

        @media (max-width: 1440px) {
            width: 100%;
            margin-left: 0;
        }

        &-content {
            max-width: rem(664);
            margin: 0 rem(23);

            @media (max-width: 1440px) {
                max-width: 85%;
                margin: 0 auto;
            }

            @media (max-width: 991px) {
                max-width: 75%;
            }

            @media (max-width: 767px) {
                max-width: 85%;
            }
        }

        p {
            margin-top: 0;
            margin-bottom: rem(18);
            padding-top: rem(15);
            font-size: rem(23);
            line-height: 120%;

            @media (max-width: 991px) {
                margin-bottom: rem(10);
                font-size: rem(18);
                line-height: rem(18);
            }

            @media (max-width: 767px) {
                margin-bottom: rem(8);
                font-size: rem(13);
                line-height: rem(15);
            }
        }

        span {
            font-size: rem(19);
            font-weight: 300;
            line-height: 120%;
            color: $grey_light_more;

            @media (max-width: 991px) {
                font-size: rem(12);
                line-height: rem(14);
            }

            @media (max-width: 767px) {
                font-size: rem(10);
                line-height: rem(12);
            }
        }
    }

    &__quote {
        font-size: rem(30);
        line-height: 1;

        @media (max-width: 991px) {
            font-size: rem(24);
        }

        @media (max-width: 768px) {
            font-size: rem(20);
        }
    }

    &__year {
        z-index: -1;
        position: absolute;
        top: 40%;
        font-size: rem(380);
        font-weight: 900;
        line-height: 1;
        color: $grey_light_more_2;
        opacity: 0.1;
        transform: translateY(-40%);

        @media (max-width: 1599px) {
            font-size: rem(300);
        }

        @media (max-width: 1440px) {
            top: 0;
            font-size: rem(200);
            font-weight: 700;
        }

        @media (max-width: 991px) {
            font-size: rem(120);
        }

        @media (max-width: 768px) {
            top: rem(-30);
        }
    }

    &__noise {
        z-index: -2;
        position: absolute;
        bottom: 0;

        img {
            @media (max-width: 767px) {
                height: rem(182);
            }
        }
    }
}
